import { useState } from 'react';
// import { styled } from '@mui/material';
import { Grid, Autocomplete, TextField, Typography } from '@mui/material';
import { printingPriceOptions } from 'helpers/printingPrice';
import { OneElement } from 'components/OneElement/OneElement';

export const App = () => {
  const [quality, setQuality] = useState([]);
  const [qualityValue, setQualityValue] = useState('');
  const [printingPrice, setPrintingPrice] = useState('');
  const [laminationPrice, setLaminationPrice] = useState('');

  return (
    <Grid container p="32px" gap="16px">
      <Grid item container spacing={2}>
        <Grid item xs={5}>
          <Typography variant="subtitle2"> *</Typography>
        </Grid>
        <Grid item xs={3}>
          <Typography variant="subtitle2">  *</Typography>
        </Grid>
        <Grid item xs={2}>
          <Typography variant="subtitle2"> </Typography>
        </Grid>
        <Grid item xs={2}>
          <Typography variant="subtitle2"> </Typography>
        </Grid>
      </Grid>
      <Grid item container spacing={2}>
        <Grid item xs={5}>
          <Autocomplete
            size="small"
            options={printingPriceOptions}
            getOptionLabel={option => option.material}
            renderInput={params => (
              <TextField {...params} label=" " />
            )}
            onChange={(_, newValue) => {
              setQuality(newValue.quality);
              setQualityValue('');
              setPrintingPrice('');
              setLaminationPrice(newValue.lamination_cost);
            }}
          />
        </Grid>
        <Grid item xs={3}>
          <Autocomplete
            inputValue={qualityValue}
            size="small"
            options={quality}
            getOptionLabel={option => option.label}
            renderInput={params => (
              <TextField {...params} label=" " />
            )}
            onChange={(_, newValue) => {
              setPrintingPrice(newValue.price);
              setQualityValue(newValue.label);
            }}
          />
        </Grid>
        <Grid item xs={2}>
          <TextField
            fullWidth
            size="small"
            label="./.."
            variant="outlined"
            value={printingPrice}
          />
        </Grid>
        <Grid item xs={2}>
          <TextField
            fullWidth
            size="small"
            label="./.."
            variant="outlined"
            value={laminationPrice}
          />
        </Grid>
      </Grid>
      <Grid item>
        <Typography>
                
        </Typography>
      </Grid>
      <Grid item container spacing={2} columns={8}>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
            *
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
            *
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
            
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
            
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
             
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
            
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
             
          </Typography>
        </Grid>
        <Grid item xs={1}>
          <Typography variant="subtitle2" mb={1}>
             
          </Typography>
        </Grid>
      </Grid>
      <Grid item>
        <OneElement
          printingPrice={printingPrice}
          laminationPrice={laminationPrice}
        />
      </Grid>
    </Grid>
  );
};

